// Add Google-Font (Lato for the UI)
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

$primary-color: #263276;
$border-color: #707070;

// Mixins 


// Add this to ParentElement because of Flex property.
@mixin centerElement {
    display: flex;
    justify-content: center;
    align-items: center;

}

@mixin container {
    max-width: 1440px;
    margin: auto;
}